@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap);
*{
    margin:0;
    padding: 0;
}

body{
    background-color: black;
    font-family: 'Montserrat', sans-serif;
    color: white;
}

.App{
    max-width: 400px;
    background-color: #111;
    margin: auto;
}
.section{
    padding-left: 1rem;
    padding-bottom: 2rem;
    
}

.artist-section-container{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    padding-bottom: 5rem;
}

.section-container::-webkit-scrollbar{
    display: none;
}

.section-heading{
    font-size: 1.4rem;
    padding-top: 1rem;
    padding-bottom: 1.5rem;
}

.card-container{
    padding-right: 1rem;
}

.card-sub-heading{
    font-size: 0.7rem;
    font-weight: 900;
    color: rgb(148, 147, 147);
}

.card-img{
    /* background-image: url("https://img.discogs.com/RNQZJTHxu6rBQuIfxYTF-NLoVJo=/fit-in/300x300/filters:strip_icc():format(jpeg):mode_rgb():quality(40)/discogs-images/R-8230878-1457584164-7578.jpeg.jpg"); */
    width: 130px;
    height: 130px;
    background-size: cover;
}

.card-name{
    font-weight: 800;
    font-size: .9rem;
    padding-top: 0.4rem;
}
.header{
    display: flex;
    max-width: 400px;
    margin: auto;
    padding: 0.5rem;
    flex-wrap: wrap;
}

.header-tile{
    display: flex;
    max-width: 130px;
    height: 60px;
    background-color: rgb(37, 37, 37);
    align-items: center;
    justify-content: center;
    margin: 0 1rem;
    border-radius:3px ;
    overflow: hidden;
    margin-bottom: 1rem;
    margin: 0rem auto 1rem auto;
}

.tile-img{
    width: 200px;
    height: 100%;
    background-size: cover;
}

.tile-content{
    font-size: 0.7rem;
    align-self: center;
    font-weight: 800;
    padding:0 1rem;
    width: 200px;
}
.footer{
    height: 100px;
    width: 400px;
    position: fixed;
    bottom: 0;
    background-color: rgb(24, 24, 24);
    margin-bottom: -3px;
}

.bottom-nav-icon-container{
    display: flex;
    justify-content: space-evenly;
    align-content: center;
    padding-top:1rem ;
}

.bottom-nav-icon{
    font-size: 1.3rem;
}

.player-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid black;
}

.player-icon-container{
    font-size: 1.3rem;
    margin-right: 1%;
}

.player-icon{
    padding-right: 1rem;
}

.player-img{
    background-image: url("https://i.scdn.co/image/ab67616d0000b273da50894e074ecd5ce61de0a1");
    width: 50px;
    height: 50px;
    background-size: contain;
    margin-right: 0.4rem;
}

.song-container{
    display: flex;
    align-items: center;
}

.player-heading{
    font-size: 1rem;
}
.player-sub-heading{
    font-size: 0.8rem;
    color: rgb(148, 147, 147);
}

.line{
    width: 60%;
    height: 2px;
    background-color: white;
}
.navbar{
    height: 100px;
}
.nav-gradient{
    background-image: linear-gradient(to bottom ,rgb(3, 3, 255,0.3),rgba(17, 17, 17, 1));
    height: 100px;
}

.nav-content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 1rem ;
}

.nav-heading{
    font-size: 1.6rem;
    letter-spacing: .1px;
}

.nav-icon{
    font-size: 1.25rem;
}

.nav-icon:first-child{
    margin-right: 1.3rem;
}
.section{
    padding-left: 1rem;
    padding-bottom: 2rem;
}

.section-container{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
}

.section-container::-webkit-scrollbar{
    display: none;
}

.section-heading{
    font-size: 1.4rem;
    padding-top: 1rem;
    padding-bottom: 1.5rem;
}

.card-container{
    padding-right: 1rem;
}

.card-sub-heading{
    font-size: 0.7rem;
    font-weight: 500;
    color: rgb(148, 147, 147);
}

.card-img{
    width: 130px;
    height: 130px;
    background-size: cover;
}

.card-name{
    font-weight: 800;
    font-size: .9rem;
    padding-top: 0.4rem;
}
